// src/components/Navigator.jsx
import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import IconButton from '@mui/material/IconButton';
import { useNavigate, useLocation } from 'react-router-dom';
import logo from '../assets/icons/icon_edcworld.jpg'
import { Box } from '@mui/material';

/**
 * Navigator component using MUI AppBar with three centered tabs
 * and an icon placeholder on the left.
 */
const Navigator = () => {
  const navigate = useNavigate();
  const { pathname } = useLocation();

  const routes = ['/', '/popular-edcs', '/shopping'];
  const currentTab = routes.indexOf(pathname) !== -1 ? routes.indexOf(pathname) : 0;

  const handleChange = (event, newValue) => {
    navigate(routes[newValue]);
  };

  return (
    <AppBar
      position="fixed"
      color="white"
      elevation={0}
      sx={{
        zIndex: (theme) => theme.zIndex.appBar + 1,  // 
        backgroundColor: '#000',            // 
        color: 'white'
      }}
    >
      <Toolbar sx={{ display: 'flex', alignItems: 'center' }}>
        {/*  */}
        <IconButton
          edge="start"
          color="inherit"
          aria-label="logo"
          sx={{ mr: 2 }}
        >
          <Box
            component="img"
            src={logo}
            alt="logo"
            sx={{
              // xs  32pxsm  40pxmd+  50px
              width: { xs: 32, sm: 40, md: 50 },
              // 
              height: 'auto',
            }}
          />
        </IconButton>

        {/*  Tabs */}
        <Tabs
          value={currentTab}
          onChange={handleChange}
          variant="fullWidth"
          sx={{
            flexGrow: 1,
            '& .MuiTabs-indicator': {
              backgroundColor: '#333'
            }
          }}
          textColor="inherit"
        >
          <Tab 
            label="Home" 
            sx={{
              fontSize: {xs: '0.75rem', sm: '0.875rem', md: '1rem'},
              minWidth: {xs: 0, sm: 80, md: 100},
              py: {xs: 0.5, sm:1}
            }}
          />
          <Tab 
            label="Popular EDCs" 
            sx={{
              fontSize: {xs: '0.75rem', sm: '0.875rem', md: '1rem'}
            }}
          />
          <Tab 
            label="Shopping" 
            sx={{
              fontSize: {xs: '0.75rem', sm: '0.875rem', md: '1rem'}
            }}
          />
        </Tabs>
      </Toolbar>
    </AppBar>
  );
};

export default Navigator;
